import { withStyles } from '@material-ui/core/styles';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import { Container, Paper, Typography, Button } from '@material-ui/core';
import Reverser from '../components/Reverser'
import TextAd from '../components/TextAd';
// import Wavesurfer from 'react-wavesurfer';

// In my bundle config this is setup to export to window.WaveSurfer

const useStyles = theme => ({
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2)
    },
    introWrapper: {
        textAlign: "center",
        padding: theme.spacing(2),
        marginTop: theme.spacing(4),
        marginBottom: theme.spacing(4),
    },
    text: {
        color: "rgba(0, 0, 0, 0.78)",
        lineHeight: 1.8
    },
    textSmall: {
        color: "rgba(0, 0, 0, 0.54)",
        fontSize: theme.typography.fontSize,
        marginBottom: theme.spacing(1)
    },
    hint: {
        margin: theme.spacing(2)
    },
    currText: {
        fontSize: 18,
        fontWeight: 'bold',
        lineHeight: 1.8
    },
    normalText: {
        fontSize: 14,
        color: "rgba(0, 0, 0, 0.54)",
        lineHeight: 1.8
    }
})

class ReverseChallenge extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            step: 0
        };
        this.steps = [
            {
                leftEnable: true,
                rightEnable: false,
                leftReverseHighlight: true,
                leftFillText: "  [1] ",
                rightFillText: "...",
            },
            {
                leftEnable: true,
                rightEnable: true,
                leftReverseHighlight: true,
                leftFillText: "",
                rightFillText: "  [2] ",
            },
            {
                leftEnable: false,
                rightEnable: true,
                leftReverseHighlight: false,
                leftFillText: "",
                rightFillText: "",
            }
        ]
    }
    componentDidMount() {
        document.title = "-LEMONJING";
        // (window.adsbygoogle = window.adsbygoogle || []).push({});
    }

    onRef = (ref, child) => {
        child = ref
    }

    handleRestart = () => {
        this.setState({ step: 0 })
        this.leftChild.restart()
        this.rightChild.restart()
    }

    handleLeftStatusChange = (isFinishRecord) => {
        if (isFinishRecord) {
            this.setState({ step: 1 })
        } else {
            this.setState({ step: 0 })
        }
    }
    handleRightStatusChange = (isFinishRecord) => {
        if (isFinishRecord) {
            this.setState({ step: 2 })
        } else {
            this.setState({ step: 1 })
        }
    }

    render() {
        const { classes } = this.props;
        const current = this.steps[this.state.step]
        return (
            <div>

                <Container className={classes.container} maxWidth="md">

                    <Paper className={classes.introWrapper}>
                        <Typography variant="h5" component="h2"></Typography>
                        <Typography className={classes.text}></Typography>
                        <Typography className={classes.textSmall}></Typography>
                        <Typography className={classes.textSmall}></Typography>
                    </Paper>
                    
                    {/* <ins className="adsbygoogle"
                        style={{ display: 'inline-block', width: '100%', height: 100}}
                        data-ad-client="ca-pub-5404682594654148"
                        data-ad-slot="5414879916">
                    </ins>   */}
                    
                    <div className={classes.hint}>
                        <Typography className={this.state.step === 0 ? classes.currText : classes.normalText}>1.   [<span role="img" aria-label=""></span>1] <span role="img" aria-label=""></span></Typography>
                        <Typography className={this.state.step === 1 ? classes.currText : classes.normalText}>2.   [<span role="img" aria-label=""></span>1]   []   [<span role="img" aria-label=""></span>2] </Typography>
                        <Typography className={this.state.step === 2 ? classes.currText : classes.normalText}>3.   [<span role="img" aria-label=""></span>2]   []   [<span role="img" aria-label=""></span>2]   [<span role="img" aria-label=""></span>1] </Typography>
                        {current.text}<Button size="small" variant="contained" color="primary" onClick={this.handleRestart}></Button>
                    </div>
                    
                    <br />
                    <Grid container spacing={3}>
                        <Grid item xs={6}><Reverser
                            reverseHighlight={current.leftReverseHighlight}
                            onStatusChange={this.handleLeftStatusChange}
                            onRef={(ref) => this.leftChild = ref}
                            enable={current.leftEnable}
                            title="1"
                            fillText={current.leftFillText}></Reverser>
                        </Grid>
                        <Grid item xs={6}>
                            <Reverser
                                reverseHighlight={true}
                                onStatusChange={this.handleRightStatusChange}
                                onRef={(ref) => this.rightChild = ref}
                                enable={current.rightEnable}
                                title="2"
                                fillText={current.rightFillText}>
                            </Reverser></Grid>
                    </Grid>
                    <br />
                    <TextAd/>
                    <Typography className={classes.textSmall}>.</Typography>
                </Container>
            </div>
        );
    }
}
export default withStyles(useStyles)(ReverseChallenge);