function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      <Box>{children}</Box>
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};
class ChildhoodStore extends React.Component {
  constructor(props) {
    super(props);
    const money = 100

    this.items = require('../static/childhood.json');
    const itemsNumTmp = {};
    Object.keys(this.items).forEach(name => {
      itemsNumTmp[name] = 0
    });
    this.state = {
      profitPerSec: 0,
      money: money,
      balance: money,
      itemsNum: itemsNumTmp,
      isDrawerOpen: false,
      count: 0,
      isFinished: false,
      isDialog: false
    };
    this.delay = 1000
  }

  resetItemsNum = () => {
    const itemsNumTmp = {};
    Object.keys(this.items).forEach(name => {
      itemsNumTmp[name] = 0
    });
    this.setState({
      balance: this.state.money,
      itemsNum: itemsNumTmp,
    })
  }

  componentDidMount() {
    document.title = "-LEMONJING";
    // (window.adsbygoogle = window.adsbygoogle || []).push({});
  }
  a11yProps = (index) => {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }
  numberWithCommas = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }
  numberInCn = (num) => {
    let [zheng, ling] = num.toFixed(2).toString().split('.')
    const kuai = zheng === '0' ? '' : zheng + ''
    const mao = ling.substr(0, 1) === '0' ? '' : ling.substr(0, 1) + ''
    const fen = ling.substr(1, 1) === '0' ? '' : ling.substr(1, 1) + ''
    return kuai + mao + fen
  }
  onNumberChange = (target) => {
    const itemname = target.id;
    if (!target.value || target.value == 0) { // 
      target.value = 0;
    } else if (target.value[0] == 0 || target.value[0] == '-') { // 
      target.value = target.value.substr(1)
    }
    let itemnumber = target.value;

    // 
    let currBalance = this.state.balance - (itemnumber - this.state.itemsNum[itemname]) * this.items[itemname]['price'];

    if (currBalance < 0) {
      // 0 0
      let exceptcurr = this.state.balance + this.state.itemsNum[itemname] * this.items[itemname]['price'] // 
      itemnumber = Math.floor(exceptcurr / this.items[itemname]['price']);
      target.value = itemnumber;
      currBalance = exceptcurr - this.items[itemname]['price'] * itemnumber
    }

    this.setState({
      count: this.state.count + (itemnumber - this.state.itemsNum[itemname]),
      itemsNum: {
        ...this.state.itemsNum,
        [itemname]: itemnumber
      },
      balance: currBalance,
    })
  }

  handleCheat = () => {
    this.setState({ money: this.state.money + 10000, balance: this.state.balance + 10000, isDialog: true })
    ReactGA.event({
      category: 'childhood',
      action: 'cheat'
    })
  }

  toggleDrawer = open => event => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    this.setState({ isDrawerOpen: open })
  }


  scrollToAnchor = (anchorId) => {
    if (anchorId) {
      // 
      let anchorElement = document.getElementById(anchorId);
      // id
      if (anchorElement) { anchorElement.scrollIntoView(); }
    }
  }

  handleFinish = () => {
    this.setState({ isFinished: !this.state.isFinished })
    setTimeout(() => {
      Scroll.scroller.scrollTo('desc', {
        duration: 500,
        smooth: true,
        offset: -50,
      })
    }, 100)
    ReactGA.event({
      category: 'childhood',
      action: 'finish shopping'
    })
  }

  handleUnfinish = () => {
    this.setState({ isFinished: !this.state.isFinished })
    setTimeout(() => {
      Scroll.scroller.scrollTo('snacks', {
        duration: 500,
        smooth: true,
        offset: -90,
      })
    }, 100)
  }
  render() {
    const { balance, itemsNum } = this.state;
    const { classes } = this.props;
    return (
      <div className={classes.root} >
        <ThemeProvider theme={theme}>
          <SwipeableDrawer
            anchor="right"
            open={this.state.isDrawerOpen}
            onClose={this.toggleDrawer(false)}
            onOpen={this.toggleDrawer(true)}
          >
            <Grid container justify="center">
              <List className={classes.shopList}>

                <ListSubheader component="div" id="nested-list-subheader">
                   - (lemonjing.com)
                </ListSubheader>
                {
                  this.state.count === 0 ? <Typography className={classes.emptyLabel}></Typography> : null
                }
                {
                  Object.keys(this.items).map(name => (itemsNum[name] == 0 ? null :
                    <ListItem key={name}>
                      <ListItemAvatar>
                        <Avatar src={require(`../static/images/childhood/items/${name}.jpg`)}></Avatar>
                      </ListItemAvatar>
                      <ListItemText primary={name} secondary={': ' + itemsNum[name]} />
                      <ButtonGroup color="primary" aria-label="outlined primary button group">
                        <Button size="small" onClick={e => {
                          const target = document.getElementById(name);
                          target.value = parseInt(document.getElementById(name).value) - 1;
                          this.onNumberChange(target)
                        }}>-</Button>
                        <Button size="small" onClick={e => {
                          const target = document.getElementById(name);
                          target.value = parseInt(document.getElementById(name).value) + 1;
                          this.onNumberChange(target)
                        }}>+</Button>
                      </ButtonGroup>
                    </ListItem>
                  ))
                }
              </List>
            </Grid>

          </SwipeableDrawer>
          <Dialog
            open={this.state.isDialog}
            onClose={() => { this.setState({ isDialog: false }) }}
            aria-labelledby=""
            aria-describedby=""
          >
            <DialogTitle id="alert-dialog-title"></DialogTitle>
            <DialogContent>
              <DialogContentText id="alert-dialog-description">
                
          </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => { this.setState({ isDialog: false }) }} color="primary">
                
          </Button>
            </DialogActions>
          </Dialog>

          <main>
            <Container className={classes.container} maxWidth="md">
              <Scroll.Element name="gamepad" style={{ textAlign: 'center', fontSize: 0 }}>
                <div><img alt="" src={require('../static/images/childhood/monitor.png')} style={{ maxWidth: 350, width: '100%', margin: '0 auto' }} /></div>

                <Gamepad style={{ margin: 20 }} onSuccess={this.handleCheat} />
              </Scroll.Element>
              <Scroll.Element name="intro">
                <Grid container justify="center" alignItems="center" className={classes.topPaper}>
                  <Box className={classes.wordsWrapper}>
                    <Typography variant="h5" className={classes.largewords}></Typography>
                    <Typography className={classes.footerText}><a href="https://www.zhihu.com/people/qian-zhui-6"></a></Typography>
                    <Typography className={classes.footerText}><a href="https://www.weibo.com/u/6774131741">bot</a> &amp; <a href="https://www.weibo.com/u/6434212347">1980s-2010s</a></Typography>
                    <br />
                    <Typography className={classes.footerText}>-</Typography>
                    <Typography></Typography>
                    <div><img alt="" src={require('../static/images/childhood/1.jpg')} className={classes.comic} /></div>
                    <Typography></Typography>
                    <div><img alt="" src={require('../static/images/childhood/2.jpg')} className={classes.comic} /></div>
                    <Typography></Typography>
                    <div><img alt="" src={require('../static/images/childhood/3.jpg')} className={classes.comic} /></div>
                    <Typography></Typography>
                    <div><img alt="" src={require('../static/images/childhood/4.jpg')} className={classes.comic} /></div>
                    <Typography></Typography>
                    <div><img alt="" src={require('../static/images/childhood/5.jpg')} className={classes.comic} /></div>
                    <Typography></Typography>
                    <br/>
                    {/* <ins className="adsbygoogle"
                      style={{ display: 'inline-block', width: 300, height: 250, margin: '12px auto' }}
                      data-ad-client="ca-pub-5404682594654148"
                      data-ad-slot="3571794136">
                    </ins> */}
                    <TextAd></TextAd>
                  </Box>
                </Grid>
              </Scroll.Element>
            </Container>
            <div>
              <Grid container alignItems='center' justify='space-between' className={classes.appbar}>
                <Grid item>
                  <Typography variant="h6" className={classes.balance}>
                    {(balance !== 0) ? "" + this.numberInCn(balance) : ""}
                  </Typography>
                </Grid>
                {(
                  this.state.isFinished ? null :
                    <Grid item>
                      <IconButton aria-label="cart" onClick={this.toggleDrawer(true)}>
                        <Badge badgeContent={this.state.count} color="secondary">
                          <ShoppingCartIcon />
                        </Badge>
                      </IconButton>
                      {/* <Button variant='outlined' color='secondary'></Button> */}
                    </Grid>
                )}
              </Grid>
              {(!this.state.isFinished) ?
                <div>
                  <Container className={classes.container} maxWidth="md">
                    <div style={{ width: '100%', textAlign: 'center' }}>
                      <img alt="" src={require('../static/images/childhood/.jpg')} style={{ width: '100%', maxWidth: 500 }} />
                    </div>


                    <Typography className={classes.smallText}>()</Typography>


                    <Scroll.Element name="snacks">
                      <Grid className={classes.snackGrid} container spacing={2}>
                        <Grid item xs={12}>
                          <Typography className={classes.areaTitle}></Typography>
                        </Grid>
                        {
                          Object.keys(this.items).map(name => ((this.items[name]['type'] !== 'snacks') ? null :
                            <Grid item key={name} xs={6} sm={4} md={3}>
                              <Card className={classes.card}>
                                <CardMedia
                                  className={classes.cardMedia}
                                  image={require(`../static/images/childhood/items/${name}.jpg`)}
                                  title={name}
                                >
                                </CardMedia>
                                <CardContent className={classes.cardContent}>
                                  <Grid container justify="space-between" alignItems="center">
                                    <Grid item>
                                      <Typography className={classes.itemName}>
                                        {name}
                                      </Typography>
                                    </Grid>
                                    <Grid item>
                                      <span className={classes.price}>{this.numberInCn(this.items[name].price)}</span>
                                    </Grid>
                                  </Grid>
                                </CardContent>
                                <CardActions className={classes.cardActions}>
                                  <TextField
                                    id={name}
                                    data-itemname={name}
                                    type="number"
                                    value={itemsNum[name]}
                                    className={classes.cardInput}
                                    margin="normal"
                                    min='0'
                                    step='1'
                                    onChange={(e) => this.onNumberChange(e.target)}
                                  />
                                  <Button id={name} className={classes.addButton} size="small" variant="contained" color="primary" disabled={(balance < this.items[name].price)}
                                    onClick={e => {
                                      const target = document.getElementById(name);
                                      target.value = parseInt(document.getElementById(name).value) + 1;
                                      this.onNumberChange(target)
                                    }}>
                                    
                                </Button>
                                </CardActions>
                              </Card>
                            </Grid>
                          ))
                        }
                      </Grid>
                    </Scroll.Element>
                    <br />

                    <Scroll.Element name="ices">
                      <Grid className={classes.snackGrid} container spacing={2}>
                        <Grid item xs={12}>
                          <Typography className={classes.areaTitle}></Typography>
                          <div><img alt="" src={require('../static/images/childhood/save_energy.png')} style={{ width: '100%', maxWidth: 220 }} /></div>
                        </Grid>

                        {
                          Object.keys(this.items).map(name => ((this.items[name]['type'] !== 'ices') ? null :
                            <Grid item key={name} xs={6} sm={4} md={3}>
                              <Card className={classes.card}>
                                <CardMedia
                                  className={classes.cardMedia}
                                  image={require(`../static/images/childhood/items/${name}.jpg`)}
                                  title={name}
                                >
                                </CardMedia>
                                <CardContent className={classes.cardContent}>
                                  <Grid container justify="space-between" alignItems="center">
                                    <Grid item>
                                      <Typography className={classes.itemName}>
                                        {name}
                                      </Typography>
                                    </Grid>
                                    <Grid item>
                                      <span className={classes.price}>{this.numberInCn(this.items[name].price)}</span>
                                    </Grid>
                                  </Grid>
                                </CardContent>
                                <CardActions className={classes.cardActions}>
                                  <TextField
                                    id={name}
                                    data-itemname={name}
                                    type="number"
                                    value={itemsNum[name]}
                                    className={classes.cardInput}
                                    margin="normal"
                                    min='0'
                                    step='1'
                                    onChange={(e) => this.onNumberChange(e.target)}
                                  />
                                  <Button id={name} className={classes.addButton} size="small" variant="contained" color="primary" disabled={(balance < this.items[name].price)}
                                    onClick={e => {
                                      const target = document.getElementById(name);
                                      target.value = parseInt(document.getElementById(name).value) + 1;
                                      this.onNumberChange(target)
                                    }}>
                                    
                        </Button>
                                </CardActions>
                              </Card>
                            </Grid>
                          ))
                        }
                      </Grid>
                    </Scroll.Element>

                    <br />

                    <Scroll.Element name="drinks">
                      <Grid className={classes.snackGrid} container spacing={1}>
                        <Grid item xs={12}>
                          <Typography className={classes.areaTitle}></Typography>
                          <div><img alt="" src={require('../static/images/childhood/cap_lifter.png')} style={{ width: '100%', maxWidth: 350 }} /></div>
                        </Grid>
                        {
                          Object.keys(this.items).map(name => ((this.items[name]['type'] !== 'drinks') ? null :
                            <Grid item key={name} xs={4} sm={3} md={2}>
                              <Card className={classes.card}>
                                <CardMedia
                                  className={classes.drinksMedia}
                                  image={require(`../static/images/childhood/items/${name}.jpg`)}
                                  title={name}
                                >
                                </CardMedia>
                                <CardContent className={classes.cardContent}>
                                  <Grid container justify="space-between" alignItems="center">
                                    <Grid item>
                                      <Typography className={classes.itemName}>
                                        {name}
                                      </Typography>
                                    </Grid>
                                    <Grid item>
                                      <span className={classes.price}>{this.numberInCn(this.items[name].price)}</span>
                                    </Grid>
                                  </Grid>
                                </CardContent>
                                <CardActions className={classes.cardActions}>
                                  <TextField
                                    id={name}
                                    data-itemname={name}
                                    type="number"
                                    value={itemsNum[name]}
                                    className={classes.cardInput}
                                    margin="normal"
                                    min='0'
                                    step='1'
                                    onChange={(e) => this.onNumberChange(e.target)}
                                  />
                                  <Button id={name} className={classes.addButton} size="small" variant="contained" color="primary" disabled={(this.state.balance < this.items[name].price)}
                                    onClick={e => {
                                      const target = document.getElementById(name);
                                      target.value = parseInt(document.getElementById(name).value) + 1;
                                      this.onNumberChange(target)
                                    }}>
                                    
                        </Button>
                                </CardActions>
                              </Card>
                            </Grid>
                          ))
                        }
                      </Grid>
                    </Scroll.Element>

                    <br />

                    <Scroll.Element name="studys">
                      <Typography className={classes.areaTitle}></Typography>
                      <Typography></Typography>
                      <CanvasDraw brushRadius={1} canvasWidth={320} canvasHeight={100} lazyRadius={0} brushColor='#000' />
                      <Grid className={classes.snackGrid} container spacing={1}>
                        {
                          Object.keys(this.items).map(name => ((this.items[name]['type'] !== 'schools') ? null :
                            <Grid item key={name} xs={6} sm={4} md={3}>
                              <Card className={classes.card}>
                                <CardMedia
                                  className={classes.cardMedia}
                                  image={require(`../static/images/childhood/items/${name}.jpg`)}
                                  title={name}
                                >
                                </CardMedia>
                                <CardContent className={classes.cardContent}>
                                  <Grid container justify="space-between" alignItems="center">
                                    <Grid item>
                                      <Typography className={classes.itemName}>
                                        {name}
                                      </Typography>
                                    </Grid>
                                    <Grid item>
                                      <span className={classes.price}>{this.numberInCn(this.items[name].price)}</span>
                                    </Grid>
                                  </Grid>
                                </CardContent>
                                <CardActions className={classes.cardActions}>
                                  <TextField
                                    id={name}
                                    data-itemname={name}
                                    type="number"
                                    value={itemsNum[name]}
                                    className={classes.cardInput}
                                    margin="normal"
                                    min='0'
                                    step='1'
                                    onChange={(e) => this.onNumberChange(e.target)}
                                  />
                                  <Button id={name} className={classes.addButton} size="small" variant="contained" color="primary" disabled={(this.state.balance < this.items[name].price)}
                                    onClick={e => {
                                      const target = document.getElementById(name);
                                      target.value = parseInt(document.getElementById(name).value) + 1;
                                      this.onNumberChange(target)
                                    }}>
                                    
                        </Button>
                                </CardActions>
                              </Card>
                            </Grid>
                          ))
                        }
                      </Grid>
                    </Scroll.Element>

                    <br />

                    <Scroll.Element name="toys">
                      <Grid className={classes.snackGrid} container spacing={2}>
                        <Grid item xs={12}>
                          <Typography className={classes.areaTitle}></Typography>
                        </Grid>
                        {
                          Object.keys(this.items).map(name => ((this.items[name]['type'] !== 'toys') ? null :
                            <Grid item key={name} xs={6} sm={4} md={3}>
                              <Card className={classes.card}>
                                <CardMedia
                                  className={classes.toysMedia}
                                  image={require(`../static/images/childhood/items/${name}.jpg`)}
                                  title={name}
                                >
                                </CardMedia>
                                <CardContent className={classes.cardContent}>
                                  <Grid container justify="space-between" alignItems="center">
                                    <Grid item>
                                      <Typography className={classes.itemName}>
                                        {name}
                                      </Typography>
                                    </Grid>
                                    <Grid item>
                                      <span className={classes.price}>{this.numberInCn(this.items[name].price)}</span>
                                    </Grid>
                                  </Grid>
                                </CardContent>
                                <CardActions className={classes.cardActions}>
                                  <TextField
                                    id={name}
                                    data-itemname={name}
                                    type="number"
                                    value={itemsNum[name]}
                                    className={classes.cardInput}
                                    margin="normal"
                                    min='0'
                                    step='1'
                                    onChange={(e) => this.onNumberChange(e.target)}
                                  />
                                  <Button id={name} className={classes.addButton} size="small" variant="contained" color="primary" disabled={(balance < this.items[name].price)}
                                    onClick={e => {
                                      const target = document.getElementById(name);
                                      target.value = parseInt(document.getElementById(name).value) + 1;
                                      this.onNumberChange(target)
                                    }}>
                                    
                        </Button>
                                </CardActions>
                              </Card>
                            </Grid>
                          ))
                        }
                      </Grid>
                    </Scroll.Element>

                    <br />

                    <Scroll.Element name="pay" style={{ textAlign: 'center' }}>
                      <Button variant="contained" color="secondary" onClick={this.handleFinish}></Button>
                    </Scroll.Element>
                  </Container>
                </div>
                :
                <div style={{ textAlign: 'center' }}>
                  <Scroll.Element name="desc">
                    {
                      this.state.count === 0 ? <Typography className={classes.desc}></Typography> : null
                    }
                    {
                      Object.keys(this.items).map(name => (itemsNum[name] == 0 ? null :
                        <div key={name} >
                          <Typography className={classes.desc}>
                            {this.items[name]['desc'].replace(/#num/g, itemsNum[name])}
                          </Typography>
                          <Avatar src={require(`../static/images/childhood/items/${name}.jpg`)} style={{ display: 'inline-block' }}></Avatar>
                        </div>
                      ))
                    }
                  </Scroll.Element>

                  {(balance !== 0) ? <Typography className={classes.desc}>  <b>{this.numberInCn(balance)}</b></Typography> :
                    <Typography className={classes.desc}></Typography>}
                  <div style={{ fontSize: 0 }} >
                    <img alt="" src={require('../static/images/childhood/.svg')} style={{ width: '100%' }} />
                    <div className={classes.reality}>

                      <Typography className={classes.desc}></Typography>
                      <Typography>-</Typography>
                      <Typography variant="h5" className={classes.largewords}></Typography>
                      <Typography>-</Typography>
                    </div>
                  </div>
                </div>
              }

            </div>
          </main>
          <div style={this.state.isFinished ? { backgroundColor: '#fff', padding: 12 } : { padding: 12 }}>
            <Typography className={classes.desc} >
              <b></b> - 100
            </Typography>

            <Typography className={classes.desc} >
              <Scroll.Link to="gamepad" smooth={true} duration={500} offset={-90}></Scroll.Link>
          </Typography>
            <Typography className={classes.desc} >
              
          </Typography>
          </div>
          <Grid container alignItems='center' justify='space-between' className={classes.navbar} style={this.state.isFinished ? { backgroundColor: '#fff' } : null}>

            {
              (!this.state.isFinished) ?
                <ButtonGroup color="primary" size='medium' className={classes.navbarBtnGroup} aria-label="outlined primary button group">
                  <Button onClick={Scroll.animateScroll.scrollToTop}><PublishIcon /></Button>
                  <Button component={Scroll.Link} activeClass={classes.active} to="snacks" spy={true} smooth={true} duration={500} offset={-50}></Button>
                  <Button component={Scroll.Link} activeClass={classes.active} to="ices" spy={true} smooth={true} duration={500} offset={-50}></Button>
                  <Button component={Scroll.Link} activeClass={classes.active} to="drinks" spy={true} smooth={true} duration={500} offset={-50}></Button>
                  <Button component={Scroll.Link} activeClass={classes.active} to="studys" spy={true} smooth={true} duration={500} offset={-50}></Button>
                  <Button component={Scroll.Link} activeClass={classes.active} to="toys" spy={true} smooth={true} duration={500} offset={-50}></Button>
                  <Button onClick={Scroll.animateScroll.scrollToBottom}></Button>
                </ButtonGroup> :
                <ButtonGroup variant="contained" color="secondary" className={classes.navbarBtnGroup} aria-label="outlined primary button group">
                  <Button onClick={this.handleUnfinish}></Button>
                </ButtonGroup>
            }
          </Grid>
          {/* <Container maxWidth='md'>
            <ins className="adsbygoogle"
              style={{ display: 'inline-block', width: '100%', height: 100, margin: '10px auto' }}
              data-ad-client="ca-pub-5404682594654148"
              data-ad-slot="5677084858">
            </ins>
          </Container> */}
        </ThemeProvider >
      </div >
    );
  }

}

export default withStyles(style)(ChildhoodStore);