import { withStyles } from '@material-ui/core/styles';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import { Paper, Typography } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import MicIcon from '@material-ui/icons/Mic';
import StopIcon from '@material-ui/icons/Stop';
import DeleteIcon from '@material-ui/icons/Delete';
import Waveform from './Waveform';
import ReactGA from '../components/ReactGaWrapper';

const useStyles = theme => ({
    wrapper: {
        textAlign: "center",
        padding: theme.spacing(2),
        height: 530,
        overflow: 'hidden'
    },
    fill: {
        backgroundColor: '#e8efbd6',
        marginTop: theme.spacing(2),
        height: 317,
    },
    recordTimer: {
        marginTop: theme.spacing(2),
        paddingTop: 20,
        paddingBottom: 20,
        backgroundColor: "#edfbd6",
        color: "#689f0f"
    },
    visualizer: {
        width: '100%',
        height: 100,
        backgroundColor: '#edfbd6',
        position: 'relative',
        marginBottom: theme.spacing(2),
    },
    visualizerCanvas: {
        width: '100%',
        height: '100%',
    },
    recordingIndicator: {
        width: 20,
        height: 20,
        borderRadius: '50%',
        backgroundColor: '#f67280',
        display: 'inline-block',
        marginRight: theme.spacing(1),
        animation: '$pulse 1.5s infinite',
    },
    '@keyframes pulse': {
        '0%': {
            opacity: 1,
        },
        '50%': {
            opacity: 0.5,
        },
        '100%': {
            opacity: 1,
        },
    },
});

class Reverser extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            status: "",
            audioSrc: "",
            reverseSrc: "",
            clientWidth: document.body.clientWidth,
            second: 0,
            analyserData: new Uint8Array(0),
        };
        this.timer = null;
        this.mediaRecorder = null;
        this.audioChunks = [];
        this.audioContext = null;
        this.analyser = null;
        this.canvasRef = React.createRef();
        this.animationFrame = null;
    }

    componentDidMount() {
        this.props.onRef(this);
        if (this.canvasRef.current) {
            this.canvasContext = this.canvasRef.current.getContext('2d');
        }
    }

    componentWillUnmount() {
        this.stopRecording();
        cancelAnimationFrame(this.animationFrame);
        if (this.audioContext) {
            this.audioContext.close();
        }
    }

    startRecording = async () => {
        try {
            const stream = await navigator.mediaDevices.getUserMedia({ audio: true });

            // Create audio context and analyser for visualization
            this.audioContext = new (window.AudioContext || window.webkitAudioContext)();
            this.analyser = this.audioContext.createAnalyser();
            const source = this.audioContext.createMediaStreamSource(stream);
            source.connect(this.analyser);

            // Configure analyser
            this.analyser.fftSize = 256;
            const bufferLength = this.analyser.frequencyBinCount;
            this.setState({ analyserData: new Uint8Array(bufferLength) });

            // Start visualization
            this.visualize();

            // Setup MediaRecorder
            this.mediaRecorder = new MediaRecorder(stream);
            this.audioChunks = [];

            this.mediaRecorder.addEventListener('dataavailable', event => {
                this.audioChunks.push(event.data);
            });

            this.mediaRecorder.addEventListener('stop', () => {
                const audioBlob = new Blob(this.audioChunks, { type: 'audio/wav' });
                const audioUrl = URL.createObjectURL(audioBlob);
                this.setState({ audioSrc: audioUrl });

                this.createReverseAudio(audioBlob);

                if (stream.getTracks) {
                    stream.getTracks().forEach(track => track.stop());
                }

                ReactGA.event({
                    category: 'reverse',
                    action: 'stop record'
                });
            });

            this.mediaRecorder.start(100);
            this.setState({ status: "recording", second: 0 });

            // Start timer
            this.timer = setInterval(() => {
                this.setState({
                    second: Math.round((this.state.second + 0.01) * 100) / 100
                });
            }, 10);

        } catch (err) {
            alert("" + err.message);
            ReactGA.event({
                category: 'reverse',
                action: 'error'
            });
        }
    };

    stopRecording = () => {
        if (this.mediaRecorder && this.mediaRecorder.state !== 'inactive') {
            this.mediaRecorder.stop();
        }
        if (this.timer) {
            clearInterval(this.timer);
        }
        cancelAnimationFrame(this.animationFrame);
        this.resetVisualizer();
        this.setState({ status: "inactive" });
    };

    visualize = () => {
        if (!this.analyser || !this.canvasContext) return;

        const width = this.canvasRef.current.width;
        const height = this.canvasRef.current.height;
        const bufferLength = this.analyser.frequencyBinCount;
        const dataArray = new Uint8Array(bufferLength);

        this.canvasContext.clearRect(0, 0, width, height);

        const draw = () => {
            this.animationFrame = requestAnimationFrame(draw);

            this.analyser.getByteFrequencyData(dataArray);

            this.canvasContext.fillStyle = this.props.backgroundColor || '#edfbd6';
            this.canvasContext.fillRect(0, 0, width, height);

            const barWidth = (width / bufferLength) * 2.5;
            let x = 0;

            for (let i = 0; i < bufferLength; i++) {
                const barHeight = (dataArray[i] / 255) * height;

                this.canvasContext.fillStyle = this.props.strokeColor || '#689f0f';
                this.canvasContext.fillRect(x, height - barHeight, barWidth, barHeight);

                x += barWidth + 1;
            }
        };

        draw();
    };

    controlAudio = () => {
        if (this.state.status === "recording") {
            this.stopRecording();
            this.props.onStatusChange(true);
        } else {
            if (this.state.audioSrc) {
                this.restart();
                this.props.onStatusChange(false);
            } else {
                this.startRecording();
            }
        }
    };

    resetVisualizer = () => {
        // Cancel any ongoing animation frame
        cancelAnimationFrame(this.animationFrame);

        // Reset the canvas if it exists
        if (this.canvasRef.current && this.canvasContext) {
            const width = this.canvasRef.current.width;
            const height = this.canvasRef.current.height;

            // Clear the canvas with background color
            this.canvasContext.fillStyle = this.props.backgroundColor || '#edfbd6';
            this.canvasContext.fillRect(0, 0, width, height);
        }

        // Reset the analyser data
        this.setState({ analyserData: new Uint8Array(0) });
    };

    restart = () => {
        this.setState({ audioSrc: null, reverseSrc: null, second: 0 });
    };

    createReverseAudio = (audioBlob) => {
        const fileReader = new FileReader();
        const context = new (window.AudioContext || window.webkitAudioContext)();

        fileReader.onloadend = () => {
            const arrayBuffer = fileReader.result;

            // Modern promise-based API with fallback to callback
            try {
                const decodePromise = context.decodeAudioData(
                    arrayBuffer,
                    // Success callback for older browsers
                    (buffer) => {
                        try {
                            const reverse = this.reverseBuffer(buffer, context);
                            // Ensure we have valid length for the buffer
                            const duration = reverse.duration || reverse.length / reverse.sampleRate;
                            const blob = this.bufferToWave(reverse, Math.floor(reverse.sampleRate * duration));
                            this.setState({
                                reverseSrc: URL.createObjectURL(blob),
                            });
                        } catch (err) {
                            console.error("Error processing audio", err);
                            alert("");
                        }
                    },
                    // Error callback for older browsers
                    (error) => {
                        console.error("Error decoding audio data", error);
                        alert("");
                    }
                );

                // For browsers that support promise-based decodeAudioData
                if (decodePromise instanceof Promise) {
                    decodePromise.then(buffer => {
                        try {
                            const reverse = this.reverseBuffer(buffer, context);
                            const duration = reverse.duration || reverse.length / reverse.sampleRate;
                            const blob = this.bufferToWave(reverse, Math.floor(reverse.sampleRate * duration));
                            this.setState({
                                reverseSrc: URL.createObjectURL(blob),
                            });
                        } catch (err) {
                            console.error("Error processing audio", err);
                            alert("");
                        }
                    }).catch(error => {
                        console.error("Error decoding audio data", error);
                        alert("");
                    });
                }
            } catch (err) {
                console.error("Fatal error processing audio", err);
                alert("");
            }
        };

        fileReader.readAsArrayBuffer(audioBlob);
    };

    reverseBuffer = (buffer, context) => {
        const reverse = context.createBuffer(
            buffer.numberOfChannels,
            buffer.length,
            buffer.sampleRate
        );

        for (let channelIndex = 0; channelIndex < buffer.numberOfChannels; channelIndex++) {
            const dest = reverse.getChannelData(channelIndex);
            const src = buffer.getChannelData(channelIndex);
            const bufferLength = buffer.length;

            // Safely reverse the buffer
            for (let sampleIndex = 0; sampleIndex < bufferLength; sampleIndex++) {
                // Make sure we're within bounds for both source and destination
                const reverseIndex = bufferLength - sampleIndex - 1;
                if (reverseIndex >= 0 && reverseIndex < bufferLength) {
                    dest[sampleIndex] = src[reverseIndex];
                }
            }
        }

        return reverse;
    };

    bufferToWave = (abuffer, len) => {
        // Calculate the actual number of samples
        const actualLength = Math.min(len, abuffer.length);

        const numOfChan = abuffer.numberOfChannels;
        // Calculate data size - each sample is 2 bytes (16-bit)
        const dataSize = actualLength * numOfChan * 2;
        // Total file size includes the header (44 bytes) + data
        const fileSize = dataSize + 44;

        const buffer = new ArrayBuffer(fileSize);
        const view = new DataView(buffer);
        const channels = [];
        let pos = 0;

        // Helper functions to write data
        function setUint16(data) {
            view.setUint16(pos, data, true);
            pos += 2;
        }

        function setUint32(data) {
            view.setUint32(pos, data, true);
            pos += 4;
        }

        // Write WAVE header
        setUint32(0x46464952);                         // "RIFF"
        setUint32(fileSize - 8);                       // file length - 8
        setUint32(0x45564157);                         // "WAVE"

        setUint32(0x20746d66);                         // "fmt " chunk
        setUint32(16);                                 // length = 16
        setUint16(1);                                  // PCM (uncompressed)
        setUint16(numOfChan);
        setUint32(abuffer.sampleRate);
        setUint32(abuffer.sampleRate * 2 * numOfChan); // avg. bytes/sec
        setUint16(numOfChan * 2);                      // block-align
        setUint16(16);                                 // 16-bit (hardcoded in this demo)

        setUint32(0x61746164);                         // "data" - chunk
        setUint32(dataSize);                           // chunk length

        // Get channel data
        for (let i = 0; i < numOfChan; i++) {
            channels.push(abuffer.getChannelData(i));
        }

        // Write interleaved data
        for (let sampleIndex = 0; sampleIndex < actualLength; sampleIndex++) {
            // Check if we're going to exceed bounds
            if (pos + (numOfChan * 2) > fileSize) {
                break;
            }

            for (let channelIndex = 0; channelIndex < numOfChan; channelIndex++) {
                // Make sure we don't go out of bounds for the channel data
                if (sampleIndex < channels[channelIndex].length) {
                    const sample = Math.max(-1, Math.min(1, channels[channelIndex][sampleIndex])); // clamp
                    const value = (0.5 + sample < 0 ? sample * 32768 : sample * 32767) | 0; // scale to 16-bit signed int
                    view.setInt16(pos, value, true);  // write 16-bit sample
                    pos += 2;
                } else {
                    // If we're beyond channel data, write silence
                    view.setInt16(pos, 0, true);
                    pos += 2;
                }
            }
        }

        // Create Blob with the buffer
        return new Blob([buffer], { type: "audio/wav" });
    };

    getTime = (second) => {
        let min = Math.floor(second / 60);
        min = min < 10 ? "0" + min : min;
        let sec = second % 60;
        sec = sec.toFixed(2);
        sec = sec < 10 ? "0" + sec : sec;
        return min + ":" + sec;
    };

    render() {
        const { classes } = this.props;
        const { status, audioSrc, reverseSrc } = this.state;

        const wavepara = {
            backgroundColor: this.props.backgroundColor,
            waveColor: '#a5a5a5',
            progressColor: this.props.strokeColor,
            cursorColor: this.props.strokeColor,
            barWidth: 3,
            cursorWidth: 1,
            height: this.props.waveHeight,
            barGap: 1
        };

        return (
            <div>
                <Paper className={classes.wrapper}>
                    <Typography contentEditable="true" suppressContentEditableWarning={true} variant="h6">
                        {this.props.title}
                    </Typography>

                    <div className={classes.visualizer}>
                        <canvas
                            ref={this.canvasRef}
                            className={classes.visualizerCanvas}
                            width={Math.min(375, this.state.clientWidth / 2 - 70)}
                            height={this.props.waveHeight}
                        />
                    </div>

                    <Fab
                        className="btn"
                        color='primary'
                        onClick={this.controlAudio}
                        disabled={!this.props.enable}
                    >
                        {(status === "recording")
                            ? <StopIcon />
                            : (audioSrc ? <DeleteIcon /> : <MicIcon />)
                        }
                    </Fab>

                    {status === "recording" && (
                        <Grid className={classes.recordTimer} container justify="center">
                            <div className={classes.recordingIndicator}></div>
                            <Typography variant="h6">
                                {this.getTime(this.state.second)}
                            </Typography>
                        </Grid>
                    )}

                    <div className='wrapper'>
                        {audioSrc ? (
                            <div>
                                <br />
                                <Waveform buttonname="" src={audioSrc} wavepara={wavepara} />
                            </div>
                        ) : null}

                        {reverseSrc ? (
                            <div>
                                <br />
                                <Waveform buttonname="" reverseHighlight={this.props.reverseHighlight} src={reverseSrc} wavepara={wavepara} />
                            </div>
                        ) : (
                            <div>
                                <Grid container justify="center" alignItems="center" className={classes.fill}>
                                    <Typography>{this.props.fillText}</Typography>
                                </Grid>
                            </div>
                        )}
                    </div>
                </Paper>
            </div>
        );
    }
}

Reverser.defaultProps = {
    enable: true,
    title: "",
    backgroundColor: "#edfbd6",
    strokeColor: "#689f0f",
    waveHeight: 100,
    waveName: "audioContainer",
    onStatusChange: () => { },
    reverseHighlight: false,
    fillText: ""
};

export default withStyles(useStyles)(Reverser);