import React from 'react';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Avatar from '@material-ui/core/Avatar';
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import TextField from '@material-ui/core/TextField';
import Box from '@material-ui/core/Box';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import { withStyles } from '@material-ui/core/styles';
import { RichSimulatorStyle, MySwitch } from './RichSimulatorStyle';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import Badge from '@material-ui/core/Badge';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import IconButton from '@material-ui/core/IconButton';
import ReactGA from '../components/ReactGaWrapper';
import CardActionArea from '@material-ui/core/CardActionArea';
import { Link } from "react-router-dom";
import ScrollUpButton from "react-scroll-up-button";
import PublishIcon from '@material-ui/icons/Publish';
import TextAd from '../components/TextAd';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import GitHubButton from 'react-github-btn'
import { Divider } from '@material-ui/core';
import html2canvas from 'html2canvas';

class RichSimulator extends React.Component {
  constructor(props) {
    super(props);
    this.money = 270000000000
    this.yuebaoRate = 0.00000000079
    this.ranking = require('../static/ranking.json')['ranking']

    this.items = require('../static/goods.json')['items'];
    this.items.sort((a, b) => a.price - b.price)
    const itemsNumTmp = {};
    this.items.forEach(item => {
      itemsNumTmp[item.name] = 0;
    });
    let isDisplayTmp = {}
    this.items.forEach(item => {
      isDisplayTmp[item.name] = false;
    });
    this.state = {
      profitPerSec: 0,
      isMakingProfit: false,
      money: this.money,
      balance: this.money,
      itemsNum: itemsNumTmp,
      isDisplay: isDisplayTmp,
      count: 0,
      isDrawerOpen: false,
      isFinish: false,
      imgUrl: null
    };
    this.nametoprice = {}
    this.delay = 1000
    this.items.forEach(item => {
      this.nametoprice[item.name] = item.price;
    });
  }

  getRank = () => {
    for (let i = 0; i < this.ranking.length; i++) {
      if (this.state.balance > this.ranking[i] * 100000000) {
        return i + 1;
      }
    }
    return this.ranking.length + '+ ()'
  }

  componentDidMount() {
    document.title = "2700-LEMONJING";
    // (window.adsbygoogle = window.adsbygoogle || []).push({});
  }
  a11yProps = (index) => {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }
  numberWithCommas = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }
  onNumberChange = (target) => {
    const itemname = target.id;
    if (!target.value || target.value == 0) { // 
      target.value = 0;
    } else if (target.value[0] == 0 || target.value[0] == '-') { // 
      target.value = target.value.substr(1)
    }
    let itemnumber = target.value;

    // 
    let currBalance = this.state.balance - (itemnumber - this.state.itemsNum[itemname]) * this.nametoprice[itemname];

    if (currBalance < 0) {
      // 0 0
      let exceptcurr = this.state.balance + this.state.itemsNum[itemname] * this.nametoprice[itemname] // 
      itemnumber = Math.floor(exceptcurr / this.nametoprice[itemname]);
      target.value = itemnumber;
      currBalance = exceptcurr - this.nametoprice[itemname] * itemnumber
    }

    this.setState({
      count: this.state.count + (itemnumber - this.state.itemsNum[itemname]),
      itemsNum: {
        ...this.state.itemsNum,
        [itemname]: itemnumber
      },
      balance: currBalance,
    })
  }

  handleYuebao = (ismaking) => {
    this.setState({ isMakingProfit: ismaking })
    if (ismaking) {
      this.yuebaoInterval = setInterval(() => {
        // Your custom logic here
        this.makeProfit()
      }, this.delay);
    } else {
      clearInterval(this.yuebaoInterval)
    }
  }
  makeProfit = () => {
    const pps = Math.floor(this.state.balance * this.yuebaoRate)
    this.setState({ balance: this.state.balance + pps, profitPerSec: pps, money: this.state.money + pps });
  }

  toggleDrawer = open => event => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    this.setState({ isDrawerOpen: open })

    ReactGA.event({
      category: 'rich',
      action: 'toggle drawer'
    })
  }

  handleFinish = () => {
    this.handleYuebao(false)
    this.setState({ isDrawerOpen: false, isFinish: true, imgUrl: null }, () => {
      setTimeout(() => {
        window.scrollTo(0, 0);
        html2canvas(document.getElementById('canvas')).then((canvas) => {
          const imgUrl = canvas.toDataURL();
          this.setState({ imgUrl: imgUrl });
        })
      }, 1500)
    })
    window.scrollTo(0, 0);
    ReactGA.event({
      category: 'rich',
      action: 'richfinish'
    })
  }

  handleClear = () => {
    const itemsNumTmp = {};
    this.items.forEach(item => {
      itemsNumTmp[item.name] = 0;
    });
    this.setState({ itemsNum: itemsNumTmp, balance: this.state.money, count: 0 })
  }

  render() {
    const { isMakingProfit, profitPerSec, balance, itemsNum, isFinish, money } = this.state;
    const { classes } = this.props;
    return (
      <div className={classes.root} >
        <ScrollUpButton ContainerClassName={classes.scrollUpButton} TransitionClassName={classes.scrollUpTrans}><PublishIcon color='secondary' /></ScrollUpButton>
        <SwipeableDrawer
          anchor="right"
          open={this.state.isDrawerOpen}
          onClose={this.toggleDrawer(false)}
          onOpen={this.toggleDrawer(true)}
        >
          <Grid container justify="center">
            <List className={classes.shopList}>
              <ListSubheader component="div" id="nested-list-subheader">
                 - (lemonjing.com/rich)
              </ListSubheader>
              <div style={{ textAlign: 'center' }}>
                <Button aria-label="" onClick={this.handleClear} variant='outlined' color='primary' disabled={this.state.count === 0}></Button>&emsp;
                <Button aria-label="" onClick={this.handleFinish} variant='contained' color='primary' disabled={this.state.count === 0}></Button>
              </div>
              {
                this.state.count === 0 ? <Typography className={classes.emptyLabel}></Typography> : null
              }
              {
                this.items.map(item => (itemsNum[item.name] == 0 ? null :
                  <ListItem key={item.name}>
                    <ListItemAvatar>
                      <Avatar src={require(`../static/images/items/${item.name}.jpg`)}></Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={<Typography variant="body2">{item.name}</Typography>} secondary={': ' + itemsNum[item.name]} style={{ marginRight: '6px' }} />
                    <ButtonGroup color="secondary" size="small" aria-label="outlined primary button group">
                      <Button onClick={e => {
                        const target = document.getElementById(item.name);
                        target.value = 0;
                        this.onNumberChange(target)
                      }}></Button>
                      <Button onClick={e => {
                        const target = document.getElementById(item.name);
                        target.value = parseInt(document.getElementById(item.name).value) - 1;
                        this.onNumberChange(target)
                      }}>-</Button>
                      <Button onClick={e => {
                        const target = document.getElementById(item.name);
                        target.value = parseInt(document.getElementById(item.name).value) + 1;
                        this.onNumberChange(target)
                      }}>+</Button>
                    </ButtonGroup>
                  </ListItem>
                ))
              }
              <Typography className={classes.emptyLabel}>{this.numberWithCommas(balance)}</Typography>
            </List>
          </Grid>

        </SwipeableDrawer>
        <Grid container justify='space-between' alignItems="center" className={classes.appbar}>
          <Grid item>
            <Typography variant="h5" className={classes.balance}>
              {this.numberWithCommas(balance)}
            </Typography>
            <Typography variant="body1" className={classes.balance}>
              {this.getRank()}
            </Typography>
          </Grid>
          <Grid item>
            {(
              isFinish ?
                <Button onClick={() => this.setState({ isFinish: false })}></Button>
                :
                <IconButton aria-label="cart" onClick={this.toggleDrawer(true)}>
                  <Badge badgeContent={this.state.count} color="secondary">
                    <ShoppingCartIcon />
                  </Badge>
                </IconButton>
            )}
            {/* <Button variant='outlined' color='secondary'></Button> */}
          </Grid>
        </Grid>
        <main>
          <Container className={classes.container} maxWidth="md">
            {/* <div style={{ textAlign: 'center' }}>
              <ins className="adsbygoogle"
                style={{ display: 'inline-block', width: '100%', height: 100, margin: '10px auto' }}
                data-ad-client="ca-pub-5404682594654148"
                data-ad-slot="6892092047">
              </ins>
            </div> */}
            <TextAd></TextAd>
            {(
              isFinish ?
                <div>
                  <Typography color='textSecondary' align='center'></Typography>
                  <br />
                  {/* <Paper className={classes.paySuccess}>
                    <Grid container justify="center" alignItems="center">
                      <Box className={classes.wordsWrapper}>
                        <Typography variant="h3" className={classes.largewords}></Typography>
                        <Typography variant="h5"><b>{this.numberWithCommas(this.state.money - this.state.balance - 0.26)}</b></Typography>
                        <Typography variant="subtitle1" color='textSecondary' style={{ textDecoration: 'line-through' }}>{this.numberWithCommas(this.state.money - this.state.balance)}</Typography>
                        <br />
                        <List component="nav" aria-label="secondary mailbox folders">
                          <ListItem>
                            <ListItemText primary="" />
                            <ListItemSecondaryAction>
                              -{this.numberWithCommas(this.state.money - this.state.balance)}
                            </ListItemSecondaryAction>
                          </ListItem>
                        </List>
                      </Box>
                    </Grid>
                  </Paper> */}
                  {/* <div className={classes.shopList}>
                    <Button component='a' href={this.state.imgUrl} download="" variant='contained' target='_self' variant='contained' color='secondary'></Button>
                  </div> */}
                  <Typography color='textSecondary' align='center'></Typography>
                  <br />
                  <div className={classes.finishWrapper} id="canvas">
                    {(this.state.imgUrl !== null ? <img src={this.state.imgUrl} style={{ position: 'absolute', left: 0, top: 0, zIndex: 10, width: '100%' }} /> : null)}
                    <div style={{ textAlign: 'center' }}>
                      <img src={require('../static/images/.png')} style={{ width: '100%', maxWidth: 360 }} />
                    </div>
                    <Typography align='center'></Typography>
                    <Typography align='center'><b>2700</b><b>{this.numberWithCommas(money - balance)}</b></Typography>
                    <Typography align='center'><b>{this.numberWithCommas(balance)}</b></Typography>
                    {(typeof this.getRank() === 'number' ? <Typography align='center'><b>{this.getRank()}</b></Typography> : null)}
                    {(money - this.money > 0 ? <Typography align='center'><b>{money - this.money}</b></Typography> : null)}
                    <Typography align='center'></Typography>
                    <div style={{ textAlign: 'center' }}>
                      <img src={require('../static/images/.png')} style={{ width: '100%', maxWidth: 360 }} />
                    </div>
                    <Paper className={classes.shopList}>
                      <List>
                        <ListSubheader component="div" id="nested-list-subheader">
                          
                    </ListSubheader>
                        <Divider />
                        {
                          this.state.count === 0 ? <Typography className={classes.emptyLabel}></Typography> : null
                        }
                        {
                          this.items.map(item => (itemsNum[item.name] == 0 ? null :
                            <ListItem key={item.name}>
                              <ListItemAvatar>
                                <Avatar src={require(`../static/images/items/${item.name}.jpg`)}></Avatar>
                              </ListItemAvatar>
                              <ListItemText primary={<Typography variant="body2">{item.name}</Typography>} secondary={'' + this.nametoprice[item.name] + '  ' + itemsNum[item.name]} style={{ marginRight: '6px' }} />
                              <div>
                                <Typography variant="body2" align='right'>:</Typography>
                                <Typography variant="body2" align='right'>{this.numberWithCommas(this.nametoprice[item.name] * itemsNum[item.name])}</Typography>
                              </div>
                            </ListItem>
                          ))
                        }
                        <Typography className={classes.emptyLabel}>{this.numberWithCommas(money - balance)}</Typography>
                        <Typography className={classes.emptyLabel}>{this.numberWithCommas(balance)}</Typography>
                      </List>
                    </Paper>
                  </div>
                  <br />
                </div>
                :
                <div>
                  <Paper className={classes.topPaper}>
                    <Grid container justify="center" alignItems="center">
                      <Box className={classes.wordsWrapper}>
                        <Typography variant="h3" className={classes.largewords}></Typography>
                        <Typography></Typography>
                        <br />
                        <Typography variant="body2" color="textSecondary"> </Typography>
                        <Typography variant="body2" color="textSecondary"><a href="https://neal.fun/spend/">Spend Bill Gates' Money</a></Typography>
                        <GitHubButton href="https://github.com/liust97/jackma-simulator" data-show-count="true" aria-label="Star liust97/jackma-simulator on GitHub">Github</GitHubButton>
                      </Box>
                    </Grid>
                  </Paper>
                  <Paper className={classes.yuebaoPaper}>
                    <FormControlLabel
                      className={classes.yuebaoLabel}
                      control={
                        <MySwitch
                          checked={isMakingProfit}
                          onChange={(event) => this.handleYuebao(event.target.checked)}
                          value="checkedB"
                        />
                      }
                      label=""
                    />

                    {(isMakingProfit ? <span>{profitPerSec}</span> : null)}

                  </Paper>
                  <Typography className={classes.smallText}>()</Typography>
                  <Grid className={classes.cardGrid} container spacing={2}>
                    {
                      this.items.map(item => (
                        <Grid item key={item.name} xs={6} sm={4} md={3}>
                          <Card className={classes.card}>
                            <CardMedia
                              className={classes.cardMedia}
                              image={require(`../static/images/items/${item.name}.jpg`)}
                              title={item.name}
                            />
                            <CardContent className={classes.cardContent}>
                              <Typography component="h2">
                                {item.name}
                              </Typography>
                              <Typography className={classes.price}>
                                <b>{this.numberWithCommas(item.price)}</b>
                              </Typography>
                            </CardContent>
                            <CardActions className={classes.cardActions}>
                              <TextField
                                InputLabelProps={{
                                  shrink: true,
                                }}
                                id={item.name}
                                data-itemname={item.name}
                                label=""
                                type="number"
                                value={itemsNum[item.name]}
                                className={classes.cardInput}
                                margin="normal"
                                inputProps={{ min: "0", step: "1" }}
                                onChange={(e) => this.onNumberChange(e.target)}
                              />
                              <Button id={item.name} className={classes.addButton} size="small" variant="contained" color="primary" disabled={(balance < item.price)}
                                onClick={e => {
                                  const target = document.getElementById(item.name);
                                  target.value = parseInt(document.getElementById(item.name).value) + 1;
                                  this.onNumberChange(target)
                                }}>
                                
                              </Button>
                            </CardActions>
                          </Card>
                        </Grid>
                      ))
                    }
                  </Grid>
                  <div style={{ textAlign: 'center' }}>
                    {/* <Button aria-label="cart" onClick={this.toggleDrawer(true)} color='primary'>
                      <Badge badgeContent={this.state.count} color="secondary">
                        
                      </Badge>
                    </Button> */}
                    <Button aria-label="" onClick={this.handleFinish} variant='contained' color='primary' disabled={this.state.count === 0}></Button>
                  </div>
                  <br />
                  <Typography color='textSecondary'></Typography>
                </div>
            )}
            {/* <div style={{ textAlign: 'center' }}>
              <ins className="adsbygoogle"
                style={{ display: 'inline-block', width: '100%', height: 100, margin: '10px auto' }}
                data-ad-client="ca-pub-5404682594654148"
                data-ad-slot="4619154058">
              </ins>
            </div> */}
            <hr />
            <Card className={classes.card}>
              <CardActionArea component={Link} to="/childhood" style={{ textDecoration: 'none' }} disableRipple>
                <CardContent>
                  <Typography gutterBottom variant="h5" component="h2" className={classes.webTitle}>
                    
                </Typography>

                  <Typography variant="body2" color="textSecondary" component="p">
                    2700100
                  </Typography>
                </CardContent>
              </CardActionArea>
            </Card>
          </Container>
        </main>
      </div >
    );
  }

}

export default withStyles(RichSimulatorStyle)(RichSimulator);